import rCheckBox from '@/components/common/RHX/rCheckBox/rCheckBox';
import imageSelectionList from './imageSelectionList';
export default {
	components: {
		imageSelectionList,
		rCheckBox
	},
	props: [
		'disabled',
		'itemsData',
		'indexNum',
		'dataList',
		'deleteMoveShow'
	],
	data() {
		return {
			dataLists: this.dataList,
		};
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.dataLists = a;
			},
			deep: true
		},
	},
	created() {},
	methods: {
		// 图片选择
		tapCheckBoxThr(e) {
			console.log(e, '图片选择');
			// this.dataLists = e;
			console.log('用户选择行为,非模板行为！！！！！！！！！！！！！！！！！');
			if (e.length > 0) {
				this.$emit('tap-ImageSelection', e);
			}
		},
		// 编辑触发的用户行为
		tapImageSelectionList (e) {
			console.log(e, '编辑触发的用户行为');
			this.$emit('tap-ImageSelection', e);
		}
	}
};
